  wf_kids: 'Kinder',
  wf_kids_desc: 'Speziell für Kinder und Jugendliche entwickelte Websites.',
  wf_legal: 'Recht',
  wf_legal_desc:
    'Websites mit juristischen Inhalten, beispielsweise von Anwaltskanzleien, oder Websites, auf denen Rechtsfragen diskutiert und analysiert werden.',
  wf_local_information: 'Informationen über die Region',
  wf_local_information_desc:
    'Stadtführer und touristische Informationen, einschließlich Restaurants, Informationen zur Region und zu lokalen Sehenswürdigkeiten.',
  wf_low_thc_cannabis_products: 'Cannabisprodukte mit niedrigem THC-Gehalt',
  wf_low_thc_cannabis_products_desc:
    'BrightCloud hat eine neue Kategorie für Cannabisprodukte mit niedrigem THC-Gehalt erstellt. Diese Kategorie umfasst Websites mit Inhalten, die nicht psychoaktive Produkte mit niedrigem THC-Gehalt enthalten, darunter CBD-Öle, Harze, Extrakte, Kräuter, Kapseln, Nahrungsergänzungsmittel, Lebensmittel, Getränke und Toilettenartikel/Hautpflegeprodukte. Zu dieser Kategorie können auch andere regulierte nicht psychoaktive Substanzen gehören. Werden psychoaktive Produkte verkauft, gelten die Kategorien „Missbrauchte Drogen“ oder „Marihuana“.',
  wf_malware_sites: 'Malware-Websites',
  wf_malware_sites_desc:
    'Websites mit bösartigen Inhalten, einschließlich ausführbarer Dateien, Websites, die durch unbeabsichtigtes Herunterladen den Computer des Benutzers infizieren, bösartige Skripts, Viren, Trojaner und Code.',
  wf_marijuana: 'Marihuana',
  wf_marijuana_desc:
    'Websites über den Konsum, den Anbau, die Geschichte und Kultur von Marihuana, einschließlich rechtlicher Fragen.',
