  wf_military_desc: 'Informationen über verschiedene militärische Bereiche, Streitkräfte und Militärgeschichte.',
  wf_misc: 'Verschiedenes',
  wf_motor_vehicles: 'Kraftfahrzeuge',
  wf_motor_vehicles_desc:
    'Kraftfahrzeugbewertungen, Tipps zum Kauf oder Verkauf von Fahrzeugen, Ersatzteilkataloge. Autohandel, Fotos, Diskussionen über Fahrzeuge, einschließlich Motorrädern, Booten, Autos, Lastwagen und Wohnmobilen. Zeitschriften und Magazine über Fahrzeugumbauten.',
  wf_music: 'Musik',
  wf_music_desc:
    'Verkauf, Vertrieb und Streaming von Musik, Informationen über Musikgruppen, Auftritte, Liedtexte und das Musikgeschäft.',
  wf_news_and_media: 'Nachrichten und Medien',
  wf_news_and_media_desc:
    'Aktuelle Ereignisse oder Themen des Tages. Dazu gehören auch Radiosender und Zeitschriften, Online-Zeitungen, Websites mit Schlagzeilen, Nachrichtendienste, personalisierte Nachrichtendienste und Wetterseiten.',
  wf_nudity: 'Nacktheit',
  wf_nudity_desc:
    'Darstellungen des nackten oder halbnackten menschlichen Körpers. Diese Darstellungen sollen nicht unbedingt erotisch wirken. Es können auch Gemälde mit nackten Menschen sein oder Fotogalerien mit Bildern künstlerischer Natur. Diese Kategorie umfasst auch Websites mit Angeboten für Nudisten oder Naturisten, die Bilder von nackten Personen enthalten.',
  wf_online_greeting_cards: 'Online-Grußkarten',
  wf_online_greeting_cards_desc: 'Websites mit Online-Grußkarten.',
  wf_parked_domains: 'Geparkte Domänen',
  wf_parked_domains_desc:
    'Geparkte Domänen sind URLs, über die begrenzte Inhalte oder Click-Through-Anzeigen gehostet werden und die zwar Einnahmen für die Hosting-Unternehmen generieren, in der Regel jedoch keine Inhalte bieten, die für Endbenutzer nützlich sind. Darunter fallen auch Websites, die sich im Aufbau befinden, Ordner und Standard-Startseiten von Webservern.',
  wf_pay_to_surf: 'Bezahltes Surfen',
  wf_pay_to_surf_desc:
    'Websites, die Benutzer in Form von Bargeld oder Sachpreisen für das Lesen oder Klicken auf bestimmte Links, E-Mails oder Webseiten bezahlen.',
  wf_peer_to_peer: 'P2P-Clients',
  wf_peer_to_peer_desc:
    'Peer-to-Peer-Clients und der Zugriff darauf. Umfasst Torrents und Programme zum Herunterladen von Musik.',
  wf_personal_sites_and_blogs: 'Persönliche Websites und Blogs',
  wf_personal_sites_and_blogs_desc:
    'Persönliche Websites, die von Einzelpersonen oder Gruppen veröffentlicht werden, sowie Blogs.',
  wf_personal_storage: 'Persönliche Speicher',
  wf_personal_storage_desc: 'Online-Speicherung und Veröffentlichung von Dateien, Musik, Bildern und anderen Daten.',
  wf_philosophy_and_political_advocacy: 'Philosophie und politische Interessenvertretung',
  wf_philosophy_and_political_advocacy_desc:
    'Politik, Philosophie, Diskussionen, Vertreten eines bestimmten Standpunkts oder einer bestimmten Haltung, um eine Sache voranzubringen.',
  wf_phishing_and_other_frauds: 'Phishing und andere Betrugsmethoden',
  wf_phishing_and_other_frauds_desc:
    'Phishing-, Pharming- und andere Websites, die sich als seriöse Sites ausgeben, um personenbezogene Daten von Benutzern zu erfassen. Diese Websites sind in der Regel ziemlich kurzlebig, sodass die Beispiele nicht lange bestehen bleiben. Kontaktieren Sie uns, wenn Sie neue Daten benötigen.',
  wf_privacy: 'Datenschutz',
  wf_productivity: 'Produktivität',
  wf_proxy_avoidance_and_anonymizers: 'Proxy-Vermeidung und Anonymisierer',
  wf_proxy_avoidance_and_anonymizers_desc:
    ' Proxyserver und andere Methoden, auf URLs zuzugreifen und dabei die URL-Filterung oder Überwachung zu umgehen. Webbasierte Übersetzungssites, die die Filterung umgehen.',
  wf_questionable: 'Fragwürdige Inhalte',
  wf_questionable_desc:
    'Geschmackloser Humor, Websites, die schnelles Geld versprechen oder den Browser oder den Client auf ungewöhnliche, unerwartete oder verdächtige Weise manipulieren.',
  wf_real_estate: 'Immobilien',
  wf_real_estate_desc:
    'Informationen zum Mieten, Kaufen oder Verkaufen von Immobilien oder Grundstücken.  Tipps zum Kauf oder Verkauf von Häusern.  Immobilienmakler, Vermietungs- oder Umzugsdienste und Renovierungs- oder Sanierungsunternehmen.',
  wf_recreation_and_hobbies: 'Freizeit und Hobbys',
  wf_recreation_and_hobbies_desc:
    'Informationen, Vereinigungen, Foren und Veröffentlichungen zu Freizeitaktivitäten, beispielsweise für Sammler, zu Modellflugzeug-Bausätzen, Outdoor-Aktivitäten wie Wandern, Camping, Klettern, speziellen künstlerischen oder handwerklichen Techniken, Informationen zu Tieren und Haustieren, einschließlich rassespezifischer Informationen, Training, Ausstellungen und Tierschutzvereinen.',
  wf_reference_and_research: 'Referenz und Forschung',
  wf_reference_and_research_desc:
    'Persönliches, berufliches oder pädagogisches Referenzmaterial, einschließlich Online-Wörterbüchern, Karten, Zensus, Jahrbüchern, Bibliothekskatalogen, Genealogie und wissenschaftlicher Informationen.',
  wf_religion: 'Religion',
  wf_religion_desc:
    'Konventionelle oder alternative religiöse oder halbreligiöse Themen sowie Kirchen, Synagogen oder andere religiöse Einrichtungen.',
  wf_search_engines: 'Suchmaschinen',
  wf_search_engines_desc:
    'Websites für die Suche mithilfe von Stichwörtern oder Ausdrücken. Die zurückgegebenen Ergebnisse können Texte, Websites, Bilder, Videos und Dateien enthalten.',
  wf_security: 'Sicherheit',
  wf_self_harm: 'Selbstbeschädigung',
  wf_self_harm_desc:
    'Durch den digitalen Konsum haben sich die Möglichkeiten für den Zugriff auf grafische Inhalte, Details zu Schadensmethoden und Inhalte, die Selbstverletzung und Selbstmord verherrlichen oder fördern, erhöht. Der Zugriff auf solche Inhalte könnte belastend sein und selbstverletzendes und suizidales Verhalten fördern oder verstärken. Mit der neuen Kategorie „Selbstverletzung“ von BrightCloud werden zur Durchsetzung von Richtlinien URLs erstellt, die Selbstverletzung wie Anorexie, Bulimie und andere Arten von Selbstverletzung fördern.',
  wf_sensitive: 'Vertraulich',
  wf_sex_education: 'Sexualerziehung',
  wf_sex_education_desc:
    'Informationen über Fortpflanzung, sexuelle Entwicklung, Safer-Sex-Praktiken, sexuell übertragbare Krankheiten, Sexualität, Geburtenkontrolle, sexuelle Entwicklung, Tipps und Produkte für besseren Sex sowie Verhütungsmittel.',
  wf_shareware_and_freeware: 'Shareware und Freeware',
  wf_shareware_and_freeware_desc:
    'Software, Bildschirmschoner, Symbole, Hintergrundbilder, Dienstprogramme, Klingeltöne. Enthält Downloads gegen eine Spende und Open-Source-Projekte.',
  wf_shopping: 'Einkaufen',
  wf_shopping_desc:
    'Kaufhäuser, Einzelhandelsgeschäfte, Firmenkataloge und andere Websites, auf denen Verbraucher oder Unternehmen online einkaufen und Waren und Dienstleistungen erwerben können.',
  wf_social_networking: 'Soziale Netzwerke',
  wf_social_networking_desc:
    'Die Websites sozialer Netzwerke mit Benutzer-Communities, in denen Benutzer interagieren, Nachrichten und Bilder posten und auf andere Art kommunizieren. Diese Websites waren früher Teil der Kategorie „Persönliche Websites und Blogs“, wurden jedoch in diese neue Kategorie verschoben, um eine Unterscheidung und genauere Richtlinien zu ermöglichen.',
  wf_society: 'Gesellschaft',
  wf_society_desc:
    'Eine Vielzahl von Themen, Gruppen und Verbänden, die für die gesamte Bevölkerung relevant sind, allgemeine Fragen, die eine Vielzahl von Menschen betreffen, einschließlich Sicherheit, Kinder, gesellschaftlicher Organisationen und philanthropischer Gruppen.',
  wf_spam_urls: 'SPAM-URLs',
  wf_spam_urls_desc: 'In SPAM enthaltene URLs',
  wf_sports: 'Sport',
  wf_sports_desc:
    'Team- oder Konferenz-Websites, internationale, nationale, Schul- und Profi-Ergebnisse, Spielpläne, sportbezogene Online-Magazine oder Newsletter, Fantasy-Sport und virtuelle Sportligen.',
